export class GlobalFunction {

    static toNameFormat(fname: string, mname: string, lname: string) {
        return `${fname} ${GlobalFunction.getIntials(mname)} ${lname}`;
    }

    static formatNumber(num: number) {
        return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
    }

    static getIntials(text: string) {
        var initials = '';
        var wordArray = text.split(' ');
        for (var i = 0; i < wordArray.length; i++) {
            initials += wordArray[i].substring(0,1).toUpperCase() + '.';
        }
        return initials;
    }

    static toCommonDateString(date: Date) {
        if (date) {
            const monthNames = [
                "January", "February", "March",
                "April", "May", "June", "July",
                "August", "September", "October",
                "November", "December"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            return (monthNames[monthIndex] + " " + day + ", " + year);
        }

        return '';
    }

    static getTimeDeffirence(fromDate: Date, toDate: Date) {
        const seconds = (toDate.getTime() - fromDate.getTime()) / 1000;
        const scaleList = [ 
            ['day', 'days'], 
            ['hour', 'hours'], 
            ['minute', 'minutes'], 
            ['second', 'seconds'] 
        ];
        const valueList = [
            Math.round(seconds / 86400),
            Math.round(seconds / 3600) % 24,
            Math.round(seconds / 60) % 60,
            Math.round(seconds) % 60
        ];
        
        let index = 0;
        for (let i = 0; i < valueList.length; i++) {
            if (valueList[i] > 0) {
                index = i;
                break;
            }
        }

        if (index) {
            return `${valueList[index]} ${scaleList[index][valueList[index] == 1 ? 0 : 1]}`
        }
        return '';
    }

    static toShortDateString(date: Date) {
        if (date) {
            const monthNames = [
                "Jan", "Feb", "Mar", "Apr", "May", "Jun", 
                "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            return (monthNames[monthIndex] + " " + day + ", " + year);
        }

        return '';
    }

    static toDateAndTime(date: Date) {
        if (date) {
            const monthNames = [
                "Jan", "Feb", "Mar", "Apr", "May", "Jun", 
                "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            const hours = date.getHours() - (date.getHours() > 12 ? 12 : 0);
            const minutes = date.getMinutes();
            const seconds = date.getSeconds();
            const meridian = (date.getHours() > 12 ? "PM" : "AM");
            return (
                monthNames[monthIndex] + " " + 
                day + ", " + 
                year + " - " + 
                hours + ":" + 
                minutes + ":" + 
                seconds + " " + 
                meridian);
        }

        return '';
    }

    static getMatchByPlanCode (nodes: any[], baseNode: any, planCode: string) {
        const planIndex = nodes.findIndex((item: any) => {
            if (baseNode.level === 8) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3] &&
                        baseNode.tree_path[4] === item.tree_path[4] &&
                        baseNode.tree_path[5] === item.tree_path[5] &&
                        baseNode.tree_path[6] === item.tree_path[6] &&
                        baseNode.tree_path[7] === item.tree_path[7];
            } else if (baseNode.level === 7) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3] &&
                        baseNode.tree_path[4] === item.tree_path[4] &&
                        baseNode.tree_path[5] === item.tree_path[5] &&
                        baseNode.tree_path[6] === item.tree_path[6];
            } else if (baseNode.level === 6) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3] &&
                        baseNode.tree_path[4] === item.tree_path[4] &&
                        baseNode.tree_path[5] === item.tree_path[5];
            } else if (baseNode.level === 5) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3] &&
                        baseNode.tree_path[4] === item.tree_path[4];
            } else if (baseNode.level === 4) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3];
            } else if (baseNode.level === 3) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2];
            } else {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level;
            } 
        });
        return planIndex;
    }


    static getNodeTree (item: any, nodes: any[]) {
        let current: any = item;
        let treePath: any[] = [];
        let levelLimit = 2;

        if (Array.isArray(current['tree_path'])) {
            treePath = [...current['tree_path']].reverse();
        } else {
            treePath = Object.values(current['tree_path']).reverse();
        }
        treePath.shift();

        if (current.level === 3) {
            levelLimit = 1;
        }

        const nodeTree = treePath.reduce((result, path) => {
            if (current !== undefined && current.level > levelLimit) {

                const input = {
                    id: current.id,
                    name: current.name,
                    code: current.code
                };

                if (current['original_id']) {
                    input['original_id'] = current['original_id'];
                }

                result.push(input);

                current = nodes.find((node: any) => {
                    return node.level === (current.level - 1) &&
                        node.code === path &&
                        node.children.includes(current.id);
                });
            }
            return result;
        }, new Array()).reverse();

        return nodeTree;
    } 

    static getDescendants (item: any, nodes: any[]) {
        let current: any = item;
        let level = current.level;

        let result: any[] = [];
        let children: any[] = current['children'] ? current['children'] : [];

        for (let x = level; x < 9; x++) {
            let newChildren: any[] = [];
            if (children.length > 0) {
                children.map((child) => {
                    const found = nodes.find((item) => {
                        return child === item.id
                    });
                    const input = {
                        id: found.id,
                        name: found.name,
                        code: found.code
                    };

                    result.push(input);

                    if (found['children'] && found['children'].length > 0) {
                        newChildren.push(...found['children']);
                    }
                });
            }
            children = newChildren;
        }

        return result;
    } 

    static filterNodesByLevel (item: any, nodes: any[], level: number) {
        if (item.level !== level) {
            return true;
        }

        if (item.tree_path[2] !== 'BASE') {
            if (level > 3) {
                if (item.tree_path[3] === 'NT' && item.included) {
                    return true;
                }
            }

            if (level === 5) {
                if (item.tree_path[3] === "RB" && item.included) {
                    return true;
                }
            }

            if (level === 8 || level === 7) {
                if (item.tree_path[4] === 'EL' && item.included) {
                    return true;
                }
            }

            if (level === 5 || level === 6) {
                if (item.tree_path[3] === 'EX' && item.included) {
                    return true;
                }
            }

            const baseIndex = GlobalFunction.getMatchByPlanCode(nodes, item, 'BASE');
            let baseNode = baseIndex > -1 ? nodes[baseIndex] : null;

            if (baseNode) {
                if (!baseNode.included && item.included) {
                    return true;
                } else if (baseNode.included && item.edited) {
                    return true;
                } else if (baseNode.included && item.included && !item.edited) {
                    const children = item.children.filter((child) => {
                        const found = nodes.find((node) => child === node.id);

                        return found ? true : false;
                    });

                    if (children.length > 0) {
                        return true;
                    }
                }
            }
        } else {
            if (item.included) {
                return true;
            }
        }
        // }

        return false;
    }

    static generateTicketNumber(ticketType: string, specificType: string, ticketNo: string) {
        const ticketTypes = {
            "Availment": "AVL",
            "Inquiry": "INQ"
        };
        const specificTypes = {
            "Inpatient": "INP",
            "Outpatient Procedure": "OP",
            "Emergency Room": "ER",
            "Maternity": "MAT",
            "Consultation": "CON"
        }
        const tempString = (ticketType === 'Inquiry' ? '' : `${specificTypes[specificType]}-`);
        return `${ticketTypes[ticketType]}-${tempString}${ticketNo}`;
    }
}
