import * as React from 'react';
import { useState, useEffect, useRef } from 'react';
import {
	Grid,
	Table,
	TableHeaderRow,
	TableFilterRow,
	Toolbar,
	SearchPanel,
	PagingPanel,
	TableSelection,
} from '@devexpress/dx-react-grid-material-ui';
import {
	PagingState,
	CustomPaging,
	FilteringState,
	SortingState,
	SearchState,
	DataTypeProvider,
	SelectionState,
	IntegratedSelection
} from '@devexpress/dx-react-grid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
	faSearch,
	faSort,
	faSortUp,
	faSortDown,
	// faPlus,
	// faCheck, 
	// faTimes
} from '@fortawesome/free-solid-svg-icons';
import { makeStyles } from '@material-ui/styles';
import * as MaterialUI from '@material-ui/core';
import { KeyboardDatePicker } from '@material-ui/pickers';
import { StatusItems } from './DropdownConstants';
//import { Components } from '@hims/core';
//#endregion Global Imports

const textFieldStyles = makeStyles({
	root: {
		'& > div': {
			backgroundColor: 'transparent',
		}, 
		'& .Mui-disabled': {
			color: "#272E4C"
		}	
	}
});

const datePickerStyles = makeStyles({
	keyboardDatePicker: {
		// marginTop: '10px',
		// marginBottom: '20px',
		width: '100%',
	},
});

const Select = ({ items, ...restProps  }: any) => {
	return (
		<MaterialUI.Select
			variant='outlined'
			style={{ width: '100%' }}
			renderValue={(selected: any) => {
				if (selected != null) {
					const value: any = items.find((item: any) => {
						return item.value === selected;
					});
					if (value) {
						return value.label;
					} else {
						return '——';
					}
				}
				return '——';
			}}
			{...restProps}
		>
			{items.map((item: any, i: number) => (
				<MaterialUI.MenuItem key={i} value={item.value}>
					{item.label}
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};

