const ClientsSearchPanel = ({ value, onValueChange }: any) => {
	return (
		<MaterialUI.OutlinedInput
			value={value ? value : ''}
			labelWidth={0}
			onChange={e => onValueChange(e.target.value ? e.target.value : null)}
			placeholder="Search clients"
			fullWidth
			startAdornment={
				<MaterialUI.InputAdornment position="start">
					<FontAwesomeIcon icon={faSearch} />
				</MaterialUI.InputAdornment>
			}
		/>
	);
};

const DefaultClientsToolbar = ({ children }: any) => {
	return <MaterialUI.Toolbar disableGutters={true} children={children} />;
};

const SortingIcon = ({ direction }: any) =>
	direction === 'asc' ? (
		<FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortUp} />
	) : (
		<FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortDown} />
	);

const ClientsSortLabel = ({ onSort, children, direction }: any) => {
	return (
		<MaterialUI.TableSortLabel hideSortIcon={true} onClick={onSort}>
			{children}
			{direction ? (
				<SortingIcon direction={direction} />
			) : (
				<FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSort} />
			)}
		</MaterialUI.TableSortLabel>
	);
};

interface ClientsTableProps {
	rows: any[];
	columns: any[];
	columnExtensions?: any[];
	sorting?: any[];
	formattedColumns?: any;
	disableSearch?: boolean;
	dropdownHeader?: boolean;
	disablePaging?: boolean;
	disableFilter?: boolean;
	disableSort?: boolean;
	toolbarType?: string | null;
  	totalCount: number;
	currentPage: number;
	limit: number;
	columnFilteringEnabled?: boolean;
	filterExtensions?: any[];
	defaultFilter?: any[];
  	onSearchChange: (value: string) => void;
  	onFilterChange: (filters: any) => void;
  	onPageChange: (currentPage: any, skip: any) => void;
  	onPageSizeChange: (value: number) => void;
  	onSortingChange: (sorting: any) => void;
  	onRowClick: (row: number) => void;
}

const defaultProps: ClientsTableProps = {
	rows: [],
	columns: [],
	columnExtensions: [],
  	filterExtensions: [],
  	sorting: [],
	formattedColumns: {},
	dropdownHeader: false,
	disableSearch: false,
	disablePaging: false,
	disableFilter: false,
	disableSort: false,
	toolbarType: null,
  	totalCount: 0,
	currentPage: 0,
	limit: 10,
	defaultFilter: [],
	columnFilteringEnabled: true,
	onSearchChange: (a) => console.log("onSearchChange", a),
	onFilterChange: (a) => console.log("onFilterChange", a),
	onPageChange: (a, b) => console.log("onPageChange", a, b),
	onPageSizeChange: (a) => console.log("onPageSizeChange", a),
	onSortingChange: (a) => console.log("onSortingChange", a),
	onRowClick: (a) => console.log("onRowClick", a),
};

export const ClientsTable: React.FC<ClientsTableProps> = (
	props: ClientsTableProps,
): JSX.Element => {
	const {
		rows,
		columns,
		formattedColumns,
		disablePaging,
		disableSearch,
		disableSort,
		disableFilter,
		toolbarType,
		columnExtensions,
		dropdownHeader,
	    totalCount,
	    currentPage,
	    limit,
	    onPageChange,
	    onPageSizeChange,
	    defaultFilter,
	    columnFilteringEnabled,
	    filterExtensions,
	    onFilterChange,
	    onSearchChange,
	    onSortingChange,
	    onRowClick,
	    sorting
	} = props;
	const [selection, setSelection] = useState<number[]>([]);
	const [pageSizes] = useState([5, 10, 15, 0]);
  	const [headerRowHeight, setHeaderRowHeight] = useState(0);
  	const [freezeHeader, setFreezeHeader] = useState(false);
	const tableHeaderRowRef = useRef(null);
  	// const [sorting, setSorting] = useState([]);

	let toolbar = DefaultClientsToolbar;

	const dataTypeProviders: any[] = [];

	function handleSelectionChange(newSelection: any[]) {
        const lastSelected = newSelection.find(
          (selected: number) => selection.indexOf(selected) === -1,
        );

        if (lastSelected !== undefined) {
        	onRowClick(lastSelected);
          	setSelection([lastSelected]);
        }
	}

	const TableComponent = ({ children, ...restProps }) => (
	  <Table.Table {...restProps} stickyHeader={freezeHeader}>
	  	{children}
	  </Table.Table>
	);

	const ContainerComponent = ({children, ...restProps}) => (
		<div {...restProps} style={{overflow: freezeHeader ? 'auto' : 'none', height: freezeHeader ? '70vh': 'auto'}}>
	      {children}
		</div>
	);

	const TableHeaderCell = ({ style, classes, children, ...restProps }: any) => {
		return (
			<MaterialUI.TableCell style={style} classes={classes} {...restProps} ref={tableHeaderRowRef}>
	  			{children}
			</MaterialUI.TableCell>
		);
	};

	// const TableFilterCell = ({ style, classes, children, ...restProps }: any) => {
	// 	return (
	// 		<MaterialUI.TableCell style={style & {top:(headerRowHeight ? headerRowHeight + 'px' : '0')}} classes={classes} {...restProps} ref={tableHeaderRowRef}>
	//   			{children}
	// 		</MaterialUI.TableCell>
	// 	);
	// };

	const ClientsFilterCell = (props: any) => {
	  	const { column, filter, onFilter, filteringEnabled, classes } = props;
		const datePickerClasses = datePickerStyles();
		if (filteringEnabled) {
			if (column.name === 'status') {
				const items = StatusItems;

				return 	(
					<MaterialUI.TableCell style={{ top: (freezeHeader && headerRowHeight ? headerRowHeight + 'px' : '0')}} classes={classes}>
						<Select
								onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
								name={'status'}
								items={items}
								id={'status'}
								value={filter ? filter.value : ''}
							/>
					</MaterialUI.TableCell>);
			} else if (column.name === 'prev_hmo_effectivity_date' || column.name === 'prev_hmo_expiry_date' ) {
				return (
					<MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' , top: (freezeHeader && headerRowHeight ? headerRowHeight + 'px' : '0')}} classes={classes}>
						<KeyboardDatePicker
							variant="dialog"
							format="MMMM DD, YYYY"
							inputVariant="outlined"
							className={datePickerClasses.keyboardDatePicker}
							value={filter ? filter.value : ''}
							invalidDateMessage=""
							onChange={date => {console.log(date); onFilter(date ? { value: date } : null)}}
						/>
					</MaterialUI.TableCell>
				);
			} else {
				return (
					<MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px', top: (freezeHeader && headerRowHeight ? headerRowHeight + 'px' : '0')}} classes={classes}>
						<MaterialUI.OutlinedInput
							classes={classes}
							style={{ width: '100%' }}
							value={filter ? filter.value : ''}
							labelWidth={0}
							onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
							placeholder=""
						/>
					</MaterialUI.TableCell>
				);
			}
		} 
		return null;
	}


	const TextFormatter = ({ value }: any) => {
		const textFieldClasses = textFieldStyles();
		return <MaterialUI.TextField value={value} classes={textFieldClasses} />;
	};

	const TextDataTypeProvider = (props: any) => (
		<DataTypeProvider formatterComponent={TextFormatter} {...props} />
	);

	if (formattedColumns !== undefined) {
		if (formattedColumns['text'] !== undefined) {
			dataTypeProviders.push(<TextDataTypeProvider for={formattedColumns.text} />);
		}
	}

	const setCurrentPageMethod = (page) => {
	    let skip = (limit * page);

	    onPageChange(page, skip);
	}


	const changePageSize = (value) => {
		if (value === 0) {
			setFreezeHeader(true);
		} else {
			setFreezeHeader(false);
		}

	    onPageSizeChange(value);
	}

	useEffect(() => {
		if (tableHeaderRowRef && tableHeaderRowRef.current) {
     		// @ts-ignore: Object is possibly 'null'.
			setHeaderRowHeight(tableHeaderRowRef.current.clientHeight);
		}
		
		// setHeaderRowHeight(tableHeaderRowRef.current.clientHeight)
	})

	return (
			<Grid rows={rows} columns={columns}>
				{dataTypeProviders}

				{/* States */}

				{!disablePaging ? (
					<PagingState
						currentPage={currentPage}
						onCurrentPageChange={setCurrentPageMethod}
						pageSize={limit}
            			onPageSizeChange={changePageSize}
					/>
				) : null}

				{!disablePaging ? <CustomPaging totalCount={totalCount} /> : null}

				{!disableSearch ? <SearchState onValueChange={onSearchChange} /> : null}

				{!disableSort ? (
					<SortingState 
						defaultSorting={[]} 
						sorting={sorting}
						onSortingChange={onSortingChange}
					/>
				) : null}

				<SelectionState
					selection={selection}
					onSelectionChange={handleSelectionChange}
				/>
				<IntegratedSelection />

				{!disableFilter ? 
					<FilteringState 
						defaultFilters={[]} 	
			            filters={
			              defaultFilter !== undefined ? defaultFilter : []
			            }
			            columnFilteringEnabled={columnFilteringEnabled}
			            columnExtensions={filterExtensions}
			            onFiltersChange={onFilterChange}
					/> : null}

				<Table tableComponent={TableComponent} containerComponent={ContainerComponent} columnExtensions={columnExtensions} />

				<TableSelection
					selectByRowClick
					showSelectionColumn={false}
				/>

				{!dropdownHeader ? (
					<TableHeaderRow
						showSortingControls={!disableSort}
						sortLabelComponent={ClientsSortLabel}
						cellComponent={TableHeaderCell}
					/> ) : null
				}
				
				{!disableFilter ? <TableFilterRow cellComponent={ClientsFilterCell} /> : null}

				{!disableSearch || toolbarType ? <Toolbar rootComponent={toolbar} /> : null}
				{!disableSearch ? <SearchPanel inputComponent={ClientsSearchPanel} /> : null}

				{!disablePaging ? <PagingPanel pageSizes={pageSizes} /> : null}
			</Grid>
	);
};

ClientsTable.defaultProps = defaultProps;
